// Simulated API Loading story
const SimulatedLoadingComponent = () => {
  const [isLoading, setIsLoading] = useState(false);
  const [data, setData] = useState<any[]>([]);

  const simulateApiCall = useCallback((searchValue: string) => {
    setIsLoading(true);
    action('API call started')(searchValue);

    // Simulate network delay (800-1500ms)
    const delay = 800 + Math.random() * 700;

    setTimeout(() => {
      const filtered = cameraData.filter(
        (item) =>
          item.code.toLowerCase().includes(searchValue.toLowerCase()) ||
          item.name.toLowerCase().includes(searchValue.toLowerCase())
      );
      setData(filtered);
      setIsLoading(false);
      action('API call completed')({results: filtered.length});
    }, delay);
  }, []);

  // Initial load
  useEffect(() => {
    simulateApiCall('');
  }, [simulateApiCall]);

  return (
    <StoryContainer>
      <StoryLabel>Simulated API Loading</StoryLabel>
      <StoryDescription>
        Type in the input to trigger a simulated API call. Watch the smooth
        transition between loading and loaded states. The spinner appears during
        fetch, and results animate in when ready.
      </StoryDescription>
      <StatusBadge $isLoading={isLoading}>
        {isLoading ? 'Fetching results...' : `${data.length} results loaded`}
      </StatusBadge>
      <AutoCompleteFilterSingleSelectMultiFieldsDisplay
        loader={isLoading}
        placeholder="Type to search (simulates API call)..."
        data={{
          list: data,
          displayKey: ['code', 'name'],
        }}
        onFilter={simulateApiCall}
        onSelect={(item) => action('Selected')(item)}
      />
    </StoryContainer>
  );
};

export const SimulatedApiLoading: Story = {
  name: 'Simulated API Loading',
  render: () => <SimulatedLoadingComponent />,
  parameters: {
    docs: {
      description: {
        story:
          'Interactive demo showing realistic API loading behavior. Each keystroke triggers a simulated API call with random delay.',
      },
    },
  },
};

// Rapid filtering story
const RapidFilteringComponent = () => {
  const [filterCount, setFilterCount] = useState(0);
  const [lastFilter, setLastFilter] = useState('');

  return (
    <StoryContainer>
      <StoryLabel>Rapid Filtering Animation</StoryLabel>
      <StoryDescription>
        Type quickly to see smooth transitions between filter states. The
        component debounces rapid input and animates between result sets
        gracefully.
      </StoryDescription>
      <div style={{marginBottom: '12px', fontSize: '13px', color: '#6b7280'}}>
        Filter count: <strong>{filterCount}</strong>
        {lastFilter && (
          <>
            {' '}
            | Last filter: <code>{lastFilter}</code>
          </>
        )}
      </div>
      <AutoCompleteFilterSingleSelectMultiFieldsDisplay
        placeholder="Type rapidly to test filtering..."
        data={{
          list: cameraData,
          displayKey: ['code', 'name'],
        }}
        onFilter={(value) => {
          setFilterCount((c) => c + 1);
          setLastFilter(value);
          action('Rapid filter')(value);
        }}
        onSelect={(item) => action('Selected')(item)}
      />
    </StoryContainer>
  );
};

export const RapidFiltering: Story = {
  name: 'Rapid Filtering',
  render: () => <RapidFilteringComponent />,
  parameters: {
    docs: {
      description: {
        story:
          'Tests the animation behavior during rapid typing. The component smoothly transitions between filter results.',
      },
    },
  },
};

